body{
  background-color: rgb(128, 199, 240);
}

.todoApp{
  background-color: coral;
  min-height: 400px;
  width:370px;
  margin:50px auto;
  border-radius: 10px;
}

#form_id input{
background-color: rgb(203, 203, 231);
border:0;
width: 210px;
height: 50px;
padding: 0 10px;
margin: 20px;
font-size: 18px;
border-radius: 10px;
outline: none;
}
 
#form_id input::placeholder{
  color:darkcyan
}

#form_id button{
  background-color: rgb(207, 133, 195);
  border:0;
  height: 47px;
  width: 70px;
  border-radius: 10px;
  font-size: 15px;
  cursor: pointer;
  font-weight: bolder;
  color:thistle bold;
}