.list{
    color: cornsilk;
    font-size: 18px;
    width:315px;
    background-color: #b5cc436b;
    min-height:40px;
    margin: 10px auto;
    border-radius: 15px;
}
 
.list p{
    padding:10px;
    position:relative;
}

.list p span{
    position:absolute;
    right: 15px;
    cursor: pointer;
}
.list p input{
    background-color: transparent;
    border:0;
    color:snow;
    font-size: 18px;
    outline: none;
}